<template>
  <div class="detailS">
    <div
      class="m-bg"
      :style="{'background-image': 'linear-gradient(to bottom, rgba(208,208,208, 0), rgba(208,208,208, .85) 30%, rgba(208,208,208, 1) 50%), url(' + detailsData.data.cover + ')'}"
    ></div>
    <div>
      <van-nav-bar :title="detailsData.data.name" left-text="返回" left-arrow @click-left="back" />
    </div>
    <div class="details-box">
      <div class="hear clearfix">
        <div class="detailsH clearfix">
          <div class="pho fl">
            <img class="auto-img" :src="detailsData.data.cover" alt />
          </div>
          <div class="text-box fl">
            <h1 class="one-text">{{detailsData.data.name}}</h1>
            <h5>作者: {{detailsData.data.author}}</h5>
            <p class="one-text">状态: {{detailsData.data.status}}</p>
            <p class="one-text">{{detailsData.data.time}}</p>
          </div>
        </div>
        <div class="details-text">
          <p>详情: {{detailsData.data.introduce}}</p>
        </div>
      </div>
      <div class="details-list">
        <div class="list-hear">
          <div class="catalog">目录</div>
        </div>
        <div class="list-text">
          <div
            class="list-t"
            v-for="(item,index) in details.listT"
            :key="index"
            @click="Goto(index)"
          >
            <p>{{item.num}}</p>
          </div>
        </div>
        <div class="list-m">
          <van-cell is-link @click="showPopup">查看更多</van-cell>
          <van-popup
            closeable
            close-icon-position="top-left"
            v-model="show"
            cposition="bottom"
            :style="{ height: '100%' }"
          >
            <div class="lsit">
              <div class="list-text">
                <div
                  class="list-t"
                  v-for="(item,index) in details.list"
                  :key="index"
                  @click="Goto(index)"
                >
                  <p>{{item.num}}</p>
                </div>
              </div>
            </div>
          </van-popup>
        </div>
      </div>
    </div>
    <div class="lick">
      <van-button
        round
        color="linear-gradient(170deg, white, #000)"
        @click="collect()"
        :text="text ? '已加入书架' : '加入书架'"
        :disabled="text"
      ></van-button>
      <van-button round color="linear-gradient(170deg, white, #e61616)" @click="Goto(index=0)">开始阅读</van-button>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("detailsModule");

export default {
  name: "detail",

  data() {
    return {
      show: false,
      list: [],
      xsurl: [],
      url: [],
      text: false,
      i: 1
    };
  },

  computed: {
    ...mapState(["display", "details", "detailsData"])
  },

  methods: {
    ...mapMutations([
      "changeDisplay",
      "detailsSet",
      "getdeteilsData",
      "resetState"
    ]),

    // 弹出框
    showPopup() {
      this.show = true;
    },

    // 返回上一步
    back() {
      this.$router.push({ name: "fiction" });
    },
    Goto(index) {
      this.i = index;
      // console.log("index ==>> ", index);
      this.$router.push({
        name: "matter",
        query: { xsurl: this.xsurl, index: this.i, xsUrl: this.url }
      });
    },
    collect() {
      let userStatus = localStorage.getItem("user");

      //  console.log("userStatus ==>> ", userStatus);

      if (userStatus) {
        userStatus = JSON.parse(userStatus);

        if (!userStatus.isLogin) {
          return this.$router.push({ name: "login" });
        }

        this.text = true;
        let data = this.detailsData;
        let list = {
          img: data.data.cover,
          name: data.data.name,
          xsUrl: this.$route.params.url,
          xsName: data.list[0].num,
          conUrl: data.list[0].url,
          i: this.i
        };

        let matterData = localStorage.getItem("collectData");
        matterData = matterData ? JSON.parse(matterData) : [];

        localStorage.setItem(
          "collectData",
          JSON.stringify([...matterData, list])
        );
      } else {
        this.$router.push({ name: "login" });
      }
    }
  },
  created() {
    let matterData = localStorage.getItem("collectData");
    matterData = matterData ? JSON.parse(matterData) : [];
    // console.log(matterData);

    //重置state
    this.resetState();

    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });

    let url;

    if (this.$route.query.url == undefined) {
      url = this.$route.params.url;
      // console.log("url ==>> ", url);
    } else {
      url = this.$route.query.url;
      // console.log("url ==>> ", url);
    }

    this.url = url;

    for (let i = 0; i < matterData.length; i++) {
      if (matterData[i].url == url) {
        this.text = true;
        break;
      }
    }

    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?xsurl1=" + url
    }).then(result => {
      // console.log("result ==>> ", result);
      this.$store.commit("detailsModule/detailsSet", result);
      this.$store.commit("detailsModule/getdeteilsData", result.data);

      this.xsurl = result.data;
      // console.log("this.xsurl ==>> ",this.xsurl )
      this.$toast.clear();
    });
    // .catch(err => {
    //   // console.log("err ==>> ", err);
    //   this.$toast.clear();
    // });
  }
};
</script>
<style>
.detailS .van-cell__value--alone {
  font-size: 18px;
}
.detailS .van-popup--center {
  width: 90%;
  height: 100%;
  background: wheat;
}
.detailS .van-nav-bar .van-icon {
  color: #e4393c;
}
.detailS .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.detailS .van-nav-bar__title {
  color: #e4393c;
  font-size: 20px;
}
.detailS .lick .van-button--normal {
  width: 150px;
  font-size: 18px;
  margin: 10px 18px;
}
</style>
<style lang="less" scoped>
.m-bg {
  position: fixed;
  top: 46px;
  bottom: 0;
  left: 0;
  right: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  filter: blur(5px);
  // opacity: .95;
}
.details-box {
  width: 100%;
  position: fixed;
  top: 46px;
  bottom: 65px;
  left: 0;
  right: 0;
  padding: 8px;
  box-sizing: border-box;
  z-index: 2;
  overflow-y: auto;

  .hear {
    width: 100%;
    // border-radius: 10px;
    background: rgba(255, 255, 255);
    overflow: hidden;
    .detailsH {
      border-bottom: 1px solid #999;
      .pho {
        width: 38%;
        margin: 5px 5px 5px 0;
      }
      .text-box {
        width: 60%;
        color: #e4393c;
        h1 {
          width: 100%;
          margin: 0;
          font-size: 30px;
          text-align: center;
          margin: 15px 0;
        }
        h5 {
          width: 100%;
          margin: 0;
          font-size: 18px;
          margin-bottom: 8px;
        }
        p {
          width: 100%;
          font-size: 18px;
          margin-bottom: 8px;
          font-weight: bold;
        }
      }
    }
    .details-text {
      font-size: 16px;
      padding: 10px;
      text-indent: 2em;
      line-height: 22px;
    }
  }
  .details-list {
    background: white;
    margin-top: 5px;
    padding: 5px;
    border-radius: 8px;
    .list-hear {
      background: white;
      .catalog {
        color: #e4393c;
        text-align: center;
        font-size: 22px;
        font-weight: bold;
        border-bottom: 1px solid #999;
        padding-bottom: 10px;
      }
    }
    .list-text {
      background: white;
      padding: 30px 5px 5px;

      .list-t {
        padding: 10px;
        border-bottom: 1px solid #999;
        p {
          font-size: 16px;
        }
      }
    }
    .list-m {
      .list-text {
        background: wheat;
        padding: 45px 5px 65px;
        .list-t {
          padding: 10px;
          border-bottom: 1px solid #999;
          &:last-child {
            border-bottom: none;
          }
          p {
            font-size: 18px;
          }
        }
      }
    }
  }
}
.lick {
  width: 100%;
  height: 65px;
  background: #b4b4b4;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 2;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
</style>